@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


body {
  font-family: "Poppins", sans-serif;
  background-color: #f8f9fa; 
  margin: 0;
  padding: 0;
}

header {
  background-color: black;
  color: #fff;
  padding: 0 35px 0 35px;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 20px;
}

nav ul li {
  display: inline;
}
nav ul li a {
  text-decoration: none;
}
a:link {
  color: white;
  background-color: transparent;
  text-decoration: none;
}
a:visited {
  color: rgb(255, 255, 255);
  background-color: transparent;
  text-decoration: none;
}

.search-bar {
  display: flex;
  align-items: center;
}

.search-bar input {
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.product-item {
  border: 1px solid #ddd;
    padding: 20px;
    margin: 10px;
    width: 200px;
    text-align: center;
    background-color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.product-item:hover {
  transform: scale(1.02);
}

.product-item img {
  width: 150px;
  height: 150px;
  object-fit: contain;
}

.product-item h2 {
  font-size: 18px;
  margin: 10px 0;
}

.product-item p {
  font-size: 14px;
  color: #555;
}

.product-item button {
  padding: 10px 15px;
  font-size: 14px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.product-item button:hover {
  background-color: #0056b3;
}

.cart {
  position: relative;
}

.cart-count {
  position: absolute;
  top: -10px;
  right: -23px;
  background: red;
  color: white;
  border-radius: 50%;
  padding: 1px 7px;
}

.cart,
.order {
  display: flex;
  align-items: center;
}
